const roleToRoute = {
  coustomer: [
    {
      name: 'Product',
    },
    {
      name: 'ProductList',
    },
    {
      name: 'ProductAdd',
    }, {
      name: 'ProductEdit',
    },
  ],
  admin: [
    {
      name: 'Product',
    },
    {
      name: 'ProductList',
    },
    {
      name: 'ProductAdd',
    },
    {
      name: 'Category',
    },
    {
      name: 'ProductEdit',
    },
  ],
};

function rejectRoutes(reference, routes) {
  return routes.filter((r) => {
    const data = r;
    if (reference.indexOf(data.name) !== -1) {
      if (data.children) {
        data.children = rejectRoutes(reference, data.children);
      }
      return true;
    }
    return false;
  });
}

export default function getMenuRoute(role, routes) {
  const roles = roleToRoute[role].map((i) => i.name);
  const newRoutes = rejectRoutes(roles, routes);
  return newRoutes;
}
