import Vue from 'vue';
import Vuex from 'vuex';
import { getCookies, setCookies, removeCookies } from '@/utils/userCookie';

Vue.use(Vuex);

function rejecthidden(payload) {
  return payload.flatMap((item) => {
    if (!item.meta.hidden) {
      let obj = {};
      if (item.children) {
        obj = {
          ...item,
          children: rejecthidden(item.children),
        };
      } else {
        obj = {
          ...item,
        };
      }
      return obj;
    }
    return [];
  });
}

export default new Vuex.Store({
  state: {
    collapsed: false,
    user: getCookies(),
    menuRoutes: [],
  },
  mutations: {
    setcollapsed(state) {
      state.collapsed = !state.collapsed;
    },
    setmenuRoutes(state, payload) {
      state.menuRoutes = payload;
    },
    setuser(state, payload) {
      state.user = payload;
    },
    removeuser(state) {
      state.user = {
        username: '',
        appkey: '',
        role: '',
        email: '',
      };
    },
  },
  actions: {
    setcollapsed({ commit }) {
      commit('setcollapsed');
    },
    setuserInfo({ commit }, info) {
      commit('setuser', info);
      setCookies(info);
    },
    removeuserInfo({ commit }) {
      commit('removeuser');
      removeCookies();
    },
    setmenuRoutes({ commit }, payload) {
      const data = rejecthidden(payload);
      commit('setmenuRoutes', data);
    },
  },
  modules: {
  },
});
