import axios from 'axios';
import urls from './Urls';

const appkey = 'q6869336_1638782157300';

const ajax = axios.create({
  baseURL: urls.baseURL,
});

ajax.interceptors.request.use((config) => {
  if (config.url.includes('/passport')) {
    return config;
  }
  return {
    ...config,
    params: {
      ...config.params,
      appkey,
    },
  };
}, (error) => Promise.reject(error));

ajax.interceptors.response.use((data) => {
  const newdata = data.data;
  if (newdata.status === 'success') {
    return newdata;
  }
  return Promise.reject(newdata.msg);
}, (error) => Promise.reject(error));

const login = (params) => ajax.post(urls.login, params);
const prolist = (params) => ajax.get(urls.prolist, { params });
const catlist = (params = () => ({})) => ajax.get(urls.catlist, { params });
const detail = (id) => ajax.get(`${urls.detail}${id}`);
const remove = (id) => ajax.delete(`${urls.detail}${id}`);
const add = (params) => ajax.post(urls.add, params);
const edit = (params) => ajax.put(urls.edit, params);

export default {
  login,
  prolist,
  catlist,
  detail,
  remove,
  add,
  edit,
};
