import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/layout/Home.vue';
import Login from '../views/login.vue';
import store from '@/store/index';
import Index from '../views/layout/content/index.vue';
import getMenuRoutes from '@/utils/permission';

Vue.use(VueRouter);

const ayncRouterMap = [
  {
    path: '/product',
    name: 'Product',
    component: Home,
    meta: {
      title: '商品',
      hidden: false,
      icon: 'inbox',
    },
    children: [
      {
        path: 'list',
        name: 'ProductList',
        component: () => import('@/views/layout/content/productList.vue'),
        meta: {
          title: '商品列表',
          hidden: false,
          icon: 'unordered-list',
        },
        children: [
          {
            path: 'edit/:id',
            name: 'ProductEdit',
            component: () => import('@/views/layout/content/productEdit.vue'),
            meta: {
              title: '编辑商品',
              hidden: true,
            },
          },
        ],
      },
      {
        path: 'add',
        name: 'ProductAdd',
        component: () => import('@/views/layout/content/productAdd.vue'),
        meta: {
          title: '添加商品',
          hidden: false,
          icon: 'file-add',
        },
      },
      {
        path: 'category',
        name: 'Category',
        component: () => import('@/views/layout/content/category.vue'),
        meta: {
          title: '类目管理',
          hidden: false,
          icon: 'project',
        },
      },
    ],
  },
];

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect: '/index',
    meta: {
      title: '首页',
      hidden: false,
      icon: 'home',
    },
    children: [
      {
        path: 'index',
        name: 'Index',
        component: Index,
        meta: {
          title: '统计',
          hidden: false,
          icon: 'number',
        },
      },
    ],
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      title: '登录',
      hidden: true,
    },
  },

];

const router = new VueRouter({
  routes,
});

let isAddRoutes = false;

router.beforeEach((to, from, next) => {
  if (to.path !== '/login') {
    if (store.state.user.username && store.state.user.appkey && store.state.user.role) {
      if (!isAddRoutes) {
        isAddRoutes = true;
        const newRot = getMenuRoutes(store.state.user.role, ayncRouterMap);
        store.dispatch('setmenuRoutes', routes.concat(newRot)).then(() => {
          router.addRoute(newRot[0]);
          return next();
        }).catch(() => { isAddRoutes = false; });
      }
      return next();
    }
    return next('/login');
  }
  return next();
});

export default router;
