<template>
  <div class="list">
    <a-menu
      :default-selected-keys="[defaultSelectKey]"
      :default-open-keys="[defaultOpenKey]"
      :selectedKeys="[defaultSelectKey]"
      mode="inline"
      theme="dark"
      :inline-collapsed="collapsed"
    >
      <a-sub-menu v-for="item in menuRoutes" :key="item.name">
        <span slot="title">
          <a-icon :type="item.meta.icon" /><span>{{item.meta.title}}</span>
        </span>
        <a-menu-item v-for="child in item.children" :key="child.name">
          <router-link :to="{name : child.name}">
            <a-icon :type="child.meta.icon" />
            {{child.meta.title}}
          </router-link>
        </a-menu-item>
      </a-sub-menu>
    </a-menu>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  computed: {
    ...mapState(['collapsed', 'menuRoutes']),
    defaultSelectKey() {
      return this.$route.matched[1] ? this.$route.matched[1].name : '';
    },
    defaultOpenKey() {
      return this.$route.matched[0].name;
    },
  },
};
</script>

<style scoped  lang="less">
.list {
  height: 100%;
  .ant-menu {
    height: 100%;
  }
  .ant-btn {
    position: absolute;
    right: 100px;
  }
}
</style>
